<template>
  <div>
    <loading :active.sync="isLoading"></loading>
      <a
        class="btn btn-general my-2"
        href="#"
        data-toggle="modal"
        data-target="#addProduct"
        @click.prevent="openModal(true)"
      ></a>
    <table class="table bg-common text-general text-left">
      <thead class="bg-general text-common">
        <tr>
          <th width="50"></th>
          <th class="d-none d-md-block border-0 text-center"></th>
          <th width="150"></th>
          <th width="100"></th>
          <!-- <th></th> -->
          <!-- <th></th> -->
          <th width="220px">ID</th>
          <!-- <th></th> -->
          <th class="text-correct"></th>
          <!-- <th></th> -->
          <th width="80"></th>
          <th></th>
          <th></th>

          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>
            <a href="#" class="text-general" @click.prevent="removeProduct(item.id)">
              <i class="fas fa-trash-alt text-common"></i>
            </a>
          </td>
          <td class="d-none d-md-block text-center">
            <img :src="item.imageUrl" class="bg-cover border border-common" alt width="100px" height="100px" srcset />
          </td>
          <td>{{item.title}}</td>
          <td>{{item.category}}</td>
          <!-- <td>{{item.content}}</td> -->
          <!-- <td>{{item.description}}</td> -->
          <td>{{item.id}}</td>
          <!-- <td>{{item.image}}</td> -->
          <td>{{item.is_enabled}}</td>
          <!-- <td>{{item.origin_price}}</td> -->
          <td>{{item.price|currency}}</td>
          <td>{{item.num}}</td>
          <td>{{item.unit}}</td>

          <td width="80">
            <a href="#" class="text-general" @click.prevent="openModal(false,item)">
              <i class="fas fa-cog"></i>
              <span></span>
            </a>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
      @keyup.enter="updateEdit"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" @click="openModal"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <!--  -->
            <form>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="inputEmail4"> Title</label>
                  <input
                    type="text"
                    class="form-control"
                    v-model="tempProduct.title"
                    id="inputEmail4"
                    placeholder=""
                    v-validate="'required'"
                    name="title"
                  />
                  <small class="text-danger" v-if="errors.has('title')"></small>
                </div>
                <div class="form-group col-md-6">
                  <label for="inputCategory"> category</label>
                  <select id="inputCategory" v-model="tempProduct.category" class="form-control">
                    <option></option>
                    <option></option>
                    <option></option>
                    <option></option>
                  </select>
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-6">
                  <label for="originPrice"> origin_price</label>
                  <input
                    type="number"
                    v-model="tempProduct.origin_price"
                    class="form-control"
                    id="originPrice"
                    v-validate="'required'"
                    name="originprice"
                  />
                  <small class="text-danger" v-if="errors.has('originprice')"></small>
                </div>
                <div class="form-group col-6">
                  <label for="price"> price</label>
                  <input
                    type="number"
                    v-model="tempProduct.price"
                    class="form-control"
                    id="price"
                    v-validate="'required'"
                    name="price"
                  />
                  <small class="text-danger" v-if="errors.has('price')"></small>
                </div>
                <div class="form-group col-3">
                  <label for="num"> num</label>
                  <input
                    type="number"
                    v-model="tempProduct.num"
                    class="form-control"
                    id="num"
                    v-validate="'required'"
                    name="num"
                  />
                  <small class="text-danger" v-if="errors.has('num')"></small>
                </div>
                <div class="form-group col-3">
                  <label for="unit"> unit</label>
                  <input
                    type="text"
                    v-model="tempProduct.unit"
                    class="form-control"
                    id="unit"
                    v-validate="'required'"
                    name="unit"
                  />
                  <small class="text-danger" v-if="errors.has('unit')"></small>
                </div>

                <div class="form-group col-6">
                  <label for="imageUpdate"> image</label>
                  <i class="fas fa-circle-notch fa-spin" v-if="isFileLoading"></i>
                  <input
                    type="file"
                    class="form-control"
                    id="imageUpdate"
                    ref="files"
                    @change="updateImage"
                    v-validate="'required'"
                    name="img"
                  />
                  <small class="text-danger" v-if="errors.has('img')"></small>
                  <img :src="tempProduct.imageUrl" class="img-fluid" />
                </div>
                <div class="form-group col-md-6">
                  <label for="inputState"> description</label>
                  <input
                    type="text"
                    v-model="tempProduct.description"
                    class="form-control"
                    id="unit"
                    v-validate="'required'"
                    name="description"
                  />
                  <small class="text-danger" v-if="errors.has('description')"></small>
                  <label for="content"> content</label>
                  <input
                    type="text"
                    v-model="tempProduct.content"
                    class="form-control"
                    id="content"
                    v-validate="'required'"
                    name="content"
                  />
                  <small class="text-danger" v-if="errors.has('content')"></small>
                </div>

                <div class="form-group col-6 my-auto">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="tempProduct.is_enabled"
                    :true-value="1"
                    :false-value="0"
                    id="is_enabled"
                    name="isenabled"
                  />
                  <!-- <small class="text-danger" v-if="errors.has('isenabled')"></small> -->
                  <label class="form-check-label" for="is_enabled"> is_enabled</label>
                </div>
                <div class="form-group">
                  <label for="imageUrl"> imageUrl</label>
                  <input
                    type="text"
                    v-model="tempProduct.imageUrl"
                    class="form-control"
                    id="imageUrl"
                    v-validate="'required'"
                    name="imgurl"
                  />
                  <small class="text-danger" v-if="errors.has('imgurl')"></small>
                </div>
              </div>
            </form>
            <!--  -->
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-general" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click.prevent="updateEdit"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  data () {
    return {
      tempProduct: {},
      isFileLoading: false,
      isNew: false
    }
  },
  methods: {
    getProducts () {
      this.$store.dispatch('getFilterProducts', '')
    },
    openModal (isNew, item) {
      if (isNew) {
        this.isNew = true
        this.tempProduct = {}
      } else {
        this.isNew = false
        this.tempProduct = Object.assign({}, item)
      }
      $('#productModal').modal('show')
    },
    updateImage () {
      const vm = this
      this.$store.dispatch('updateLoading', true)
      const updatedFile = vm.$refs.files.files[0]
      const formdata = new FormData()
      formdata.append('file-to-upload', updatedFile)
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`
      vm.$http
        .post(api, formdata, {
          // formdataheader
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(response => {
          if (response.data.success) {
            vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl)
            this.$store.dispatch('updateMessage', { message: '', status: 'correct' })
          } else {
            this.$store.dispatch('updateMessage', { message: '', status: 'correct' })
          }
          this.$store.dispatch('updateLoading', false)
        })
    },
    updateEdit () {
      const vm = this
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`
      let method = ''
      if (vm.isNew) {
        method = 'post'
      } else {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
        method = 'put'
      }
      this.$http[method](api, { data: vm.tempProduct }).then(response => {
        this.getProducts()
        this.tempProduct = {}
        $('#productModal').modal('hide')
        this.$store.dispatch('updateMessage', { message: response.data.message, status: 'correct' })
      })
    },
    removeProduct (id) {
      this.$store.dispatch('removeProduct', id)
    }
  },
  computed: {
    products () {
      return this.$store.state.productModules.products
    },
    isLoading () {
      return this.$store.state.isLoading
    }
  },
  created () {
    this.getProducts()
  }
}
</script>

<style lang="scss" scoped>
td,
img,
div {
  vertical-align: middle;
}
</style>
